import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {VerifiedMemberList} from './page/VerifiedMemberList'
import {UnverifiedMemberList} from './page/UnverifiedMemberList'
import {DetailVerifiedMember} from './page/DetailVerifiedMember'
import {DetailUnverifiedMember} from './page/DetailUnverifiedMember'
import MemberScreens from './Screens'

const MemberRoutes: React.FC = () => {
  return (
    <Switch>
      <Route path={MemberScreens.VERIFIED_LIST.PATH} component={VerifiedMemberList} />
      <Route path={MemberScreens.UNVERIFIED_LIST.PATH} component={UnverifiedMemberList} />
      <Route path={MemberScreens.DETAIL_UNVERIFIED.PATH} component={DetailUnverifiedMember} />
      <Route path={MemberScreens.DETAIL_VERIFIED.PATH} component={DetailVerifiedMember} />
      <Redirect from='/unverified' exact={true} to={MemberScreens.UNVERIFIED_LIST.PATH} />
      <Redirect from='/verified' exact={true} to={MemberScreens.VERIFIED_LIST.PATH} />
      <Redirect to={MemberScreens.VERIFIED_LIST.PATH} />
    </Switch>
  )
}

export default MemberRoutes
