import axios from 'axios'
import {BaseResponse2, Pagination2} from 'src/app/models/api.types'
import {Member} from '../models/Member'

export const url = `/api/v1/verification/users`

// export const ADMIN = `/api/v1/admins/users`
// export const ROLE = `/api/v1/admins/roles`

type VerifiedMemberListParam = {
  page: number
  limit: number
  search?: string
  statuses?: string[]
  // role_id?: string
  // cancelToken?: any
}

export function deleteVerifiedMember(id: string) {
  return axios.post(`/api/v1/verification/delete/${id}`)
}

export function getVerifiedMemberDetail(id: string) {
  return axios.get<BaseResponse2<Member>>(url + `/${id}`)
}

export function getVerifiedMemberList({
  page,
  limit,
  search,
  statuses,
}: // role_id,
// cancelToken,
VerifiedMemberListParam) {
  // return Array<Member>
  return axios.get<BaseResponse2<Pagination2<Member>>>(url + `?status=1`, {
    params: {
      page,
      limit,
      search,
      // role_id,
      statuses,
    },
    // cancelToken,
  })
}
