import axios from 'axios'
import {BaseResponse2, Pagination2} from 'src/app/models/api.types'
import {Member} from '../models/Member'

export const url = `/api/v1/verification/users`
// export const ADMIN = `/api/v1/admins/users`
// export const ROLE = `/api/v1/admins/roles`

type UnverifiedMemberListParam = {
  page: number
  limit: number
  search?: string
  statuses?: string[]
  // role_id?: string
  // cancelToken?: any
}

export function deleteUnverifiedMember(id: string) {
  return axios.post(`/api/v1/verification/delete/${id}`)
}

export function rejectUnverifiedMember(id: string) {
  return axios.post(`/api/v1/verification/${id}/reject`)
}

export function verifyUnverifiedMember(id: string) {
  return axios.post(`/api/v1/verification/${id}/verify`)
}

export function getUnverifiedMemberDetail(id: string) {
  return axios.get<BaseResponse2<Member>>(url + `/${id}`)
}

export function getUnverifiedMemberList({
  page,
  limit,
  search,
  statuses,
}: // role_id,
// cancelToken,
UnverifiedMemberListParam) {
  // return Array<Member>
  return axios.get<BaseResponse2<Pagination2<Member>>>(url + `?status=0`, {
    params: {
      page,
      limit,
      search,
      // role_id,
      statuses,
    },
    //   cancelToken,
  })
}
