//   desc: '',
//   status_verification: false,
// }

const ProfilePageTitle: FC = () => {
  return <PageTitle>Detail Unverified account</PageTitle>
}

const DetailUnverifiedMember: FC = () => {
  const history = useHistory()
  // const user = useSelector<RootState>(({auth}) => auth.user, shallowEqual) as UserModel
  // const [loading, setLoading] = useState(false)
  const {id}: any = useParams()
  const [detail, setDetail] = useState<Member | undefined>(undefined)
  const [handleApproveData, setHandleApproveData] = useState<any>(null)
  const [handleRejectData, setHandleRejectData] = useState<any>(null)
  const [showApproveMemberModal, setShowApproveMemberModal] = useState(false)
  const [showRejectMemberModal, setShowRejectMemberModal] = useState(false)
  const [success, setSuccess] = useState('')
  const [failed, setFailed] = useState('')
  const {addPageToasts} = useHeaderToast()
  const [selectedPhoto, setselectedPhoto] = useState(0)
  const fetching = useSelector<RootState>(
    ({auth}) => auth.fetchingUser,
    shallowEqual
  ) as AuthRedux.FetchingUserState
  const loaded = true
  // const [loaded, setLoaded] = useState<boolean>(true)
  // const dispatch = useDispatch()

  const album = [
    '/media/avatars/album1.png',
    '/media/avatars/album2.png',
    '/media/avatars/album3.png',
    '/media/avatars/album4.png',
    '/media/avatars/album5.png',
  ]
  const changePhoto = (idx: any) => {
    setselectedPhoto(idx)
  }
  useEffect(() => {
    document.title = getTitle('Profil Saya')
  }, [])

  useEffect(() => {
    const getData = async () => {
      // setLoading(true)
      try {
        const detail = await getUnverifiedMemberDetail(id)
        console.log('TESTING', detail)
        setDetail(detail.data.data)
        // setLoading(false)
      } catch (error) {
        // setLoading(false)
      }
    }

    getData()
  }, [id])
  // useEffect(() => {
  //   dispatch(AuthRedux.actions.requestUser())
  // }, [dispatch])
  // useEffect(() => {
  //   if (loaded === undefined && fetching === 'loading') setLoaded(false)
  //   else if (loaded === false && fetching !== 'loading') {
  //     setLoaded(true)
  //     if (fetching === 'success') {
  //       formik.setFieldValue('name', user?.name)
  //     }
  //   }
  // }, [fetching, formik, loaded, user])

  return (
    <div className='position-relative'>
      <AlertSuccess message={success} handleClose={() => setSuccess('')} />
      <AlertError message={failed} handleClose={() => setFailed('')} />
      <ProfilePageTitle />
      <div className='card'>
        <div className='card-body'>
          <div className={!loaded ? '' : 'd-none'}>Loading...</div>
          <div className={loaded && fetching === 'failed' ? '' : 'd-none'}>
            Error, try to refresh the page
          </div>
          <div className='row mt-4 mb-4'>
            <div className='col-6 text-gray-800 fw-bolder fs-7'>Selfie Photo</div>
            <div className='col-6 text-gray-800 fw-bolder fs-7'>Album/Photo</div>
          </div>
          <div className='row mt-4 mb-4'>
            <div className='col-5 text-gray-800 fw-bolder fs-7'>
              <img
                src={
                  // detail?.profile_picture ??
                  '/media/avatars/selfie.png'
                }
                alt=''
                className='img-fluid'
              />
            </div>
            <div className='col-1 text-gray-800 fw-bolder fs-7'></div>
            <div className='col-5 text-gray-800 fw-bolder fs-7'>
              <img src={album[selectedPhoto]} alt='' className='img-fluid' />
            </div>
            <div className='flex col-1 text-gray-800 fw-bolder fs-7 align-content-start'>
              <div className='row'>
                <div className='col text-gray-800 fw-bolder fs-7 '>
                  <img src={album[0]} alt='' className='img-fluid' onClick={() => changePhoto(0)} />
                </div>
              </div>
              <div className='row'>
                <div className='col text-gray-800 fw-bolder fs-7'>
                  <img src={album[1]} alt='' className='img-fluid' onClick={() => changePhoto(1)} />
                </div>
              </div>
              <div className='row'>
                <div className='col text-gray-800 fw-bolder fs-7'>
                  <img src={album[2]} alt='' className='img-fluid' onClick={() => changePhoto(2)} />
                </div>
              </div>
              <div className='row'>
                <div className='col text-gray-800 fw-bolder fs-7'>
                  <img src={album[3]} alt='' className='img-fluid' onClick={() => changePhoto(3)} />
                </div>
              </div>
              <div className='row'>
                <div className='col text-gray-800 fw-bolder fs-7'>
                  <img src={album[4]} alt='' className='img-fluid' onClick={() => changePhoto(4)} />
                </div>
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='col-6'>
              <label className='col text-gray-800 fw-bolder fs-7'>Account Info</label>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Username</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.username}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Name</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.full_name}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Nick Name</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.nickname}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Phone</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.phone_number}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Email</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.email}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Gender</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.gender}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Height</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.height}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Date of Birth</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.birthdate}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Education</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.education}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Work</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.work}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Religion</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.religion}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Zodiak</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.zodiac}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Language</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.languages}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Interested in Gender</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.gender_interest}</div>
              </div>
              <div className='row mt-4 mb-4'>
                <div className='col-4 text-gray-800 fs-7'>Description</div>
                <div className='col-8 text-gray-800 fw-bold fs-7'>{detail?.user_bio}</div>
              </div>
            </div>
          </div>
          <div className='row border-top mt-4 mb-2'>
            <div className='col-2 mt-4'>
              <button
                type='button'
                onClick={() => {
                  setHandleRejectData(id)
                  setShowRejectMemberModal(true)
                }}
                className={`btn btn-sm btn-light`}
              >
                <span className='text-secondary'>Reject</span>
              </button>
            </div>
            <div className='col-2 mt-4'>
              <button
                type='button'
                className={`btn btn-sm btn-secondary me-4`}
                onClick={() => {
                  setHandleApproveData(id)
                  setShowApproveMemberModal(true)
                }}
              >
                Approve
              </button>
            </div>
          </div>
        </div>
      </div>
      <ApproveMemberModal
        onApprove={() => {
          verifyUnverifiedMember(handleApproveData)
            .then(() => {
              //   dispatch(AdminRedux.actions.setSuccess('User berhasil dihapus.'))
              setShowApproveMemberModal(false)
              history.goBack()
            })
            .catch((err) => {
              setShowRejectMemberModal(false)
              addPageToasts({scheme: 'danger', text: getErrorMessage(err, true)})
            })
        }}
        show={showApproveMemberModal}
        handleClose={() => setShowApproveMemberModal(false)}
        data={handleApproveData}
      />
      <RejectMemberModal
        onReject={() => {
          rejectUnverifiedMember(handleRejectData)
            .then(() => {
              // dispatch(AdminRedux.actions.setSuccess('User berhasil dihapus.'))
              setShowRejectMemberModal(false)
            })
            .catch((err) => {
              setShowRejectMemberModal(false)
              addPageToasts({scheme: 'danger', text: getErrorMessage(err, true)})
            })
        }}
        show={showRejectMemberModal}
        handleClose={() => setShowRejectMemberModal(false)}
        data={handleRejectData}
      />
    </div>
  )
}

export {DetailUnverifiedMember}
